/*
 * Queue DataStructure
 * @author Lee Boonstra
 *
 *      # What do we know about Queues?
 *      It's a sequential data structure with FIFO. First In - First Out principle.
 *      (and LILO Last in - Last Out)
 *      Compare this to the line in the grocery store. Last in the line,
 *      is last person to be served.
 *
 *      ## Real world examples in Software Engineering?
 *      Printer Queue.
 */
export class Queue {
    private items: Array<any>;
    private count: number;

    constructor() {
        // Based on an Array
        this.items = [];
        // Set the size to 0;
        this.count = 0;
    }

    /*
     * Add a new item to the back of the queue.
     * Take O(1) time. We do not run any loop in any of these operations.
     *
     * @param item any
     */
    add(item: any) {
        this.items[this.count] = item;
        this.count++;
    }

    /*
     * Retrieves and removes the head of this queue.
     * Take O(1) time. We do not run any loop in any of these operations.
     *
     * @return removed item
     */
    remove() {
        if (!this.isEmpty()) {
            this.count--;
            // return this.items.pop();
            // manual remove
            const item = this.items[this.count];
            delete this.items[this.count];
            return item;
        }
    }

    /*
    * Return the the head of the queue
    * Take O(1) time. We do not run any loop in any of these operations.
    *
    * @return removed item
    */
    peek() {
        if (this.isEmpty()) return undefined;

        let i = this.count - 1;
        return this.items[i];
    }

    /*
    * Check if the Stack is empty. Returns true if the stack has no items
    * Take O(1) time. We do not run any loop in any of these operations.
    *
    * @return Boolean
    */
    isEmpty() {
        return (this.count === 0);
    }

    /*
    * Removes all the items from the Stack
    * Take O(1) time. We do not run any loop in any of these operations.
    */
    clear() {
        if (!this.isEmpty()) {
            this.items = [];
            this.count = 0;
        }
    }

    /*
    * Return the total number of items from the stack
    * Take O(1) time. We do not run any loop in any of these operations.
    *
    * @return number
    */
    size() {
        this.count = this.items.length;

        return this.count;
    }

    /*
     * Print stack
     * Take O(1) time. We do not run any loop in any of these operations.
     *
     * @return items
     */
    values() {
        return this.items;
    }
}
